/*********************************
3. Header
*********************************/

.header {
    position: fixed;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    /* width: 1318px; */
    width: 100%;
    /* height: 104px; */
    height: 80px;
    background: #FFFFFF;
    z-index: 10;
    -webkit-transition: all 200ms ease;
    -moz-transition: all 200ms ease;
    -ms-transition: all 200ms ease;
    -o-transition: all 200ms ease;
    transition: all 200ms ease;
}

.header.scrolled {
    /* top: 15px; */
}

.header.scrolled .header_content::before {
    box-shadow: 0px 20px 49px rgba(0, 0, 0, 0.17);
}

.header_content {
    width: calc(100% - 279px);
    height: 100%;
}

.header_content::before {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: '';
    /* box-shadow: 0px 20px 49px rgba(0,0,0,0.67); */
    box-shadow: 6px -2px 11px 6px rgba(0, 0, 0, 0.39);
    z-index: -1;
}


/*********************************
3.1 Logo
*********************************/

.logo img {
    height: 51px;
}

.logo_digital img {
    height: 100px;
    width: 100px;
}

.logo_container {
    display: inline-block;
    padding-left: 76px;
}

.logo span {
    font-family: 'Open Sans', sans-serif;
    font-size: 30px;
    font-weight: 900;
    color: #00267c;
    vertical-align: middle;
    text-transform: uppercase;
    margin-left: 3px;
}

.logo-menu {
    background: #00267c;
    width: 279px;
    height: 100%;
}


/*********************************
3.2 Main Nav
*********************************/

.main_nav_container {
    display: inline-block;
    margin-left: auto;
    margin-right: auto;
    /* padding-left: 93px; */
}

.main_nav {
    margin-top: 7px;
}

.main_nav_item {
