/************
3. 1380px
************/

@media only screen and (max-width: 1380px) {
    .header {
        width: 100%;
    }
    .header_content {
        width: calc(100% - 219px);
    }
    .main_nav_container {
        padding-right: 63px;
    }
    .header_side {
        width: 219px;
    }
    .header_side span {
        font-size: 14px;
    }
    .header_side img {
        width: 20px;
        height: 20px;
    }
    .home_content {
        top: 22%;
    }
}


/************
3. 1280px
************/

@media only screen and (max-width: 1280px) {
    .header {
        width: 100%;
    }
    .header_content {
        width: 100%;
    }
    .header_side {
        display: none !important;
    }
    .main_nav_container {
        padding-right: 53px;
    }
    .main_nav_item {
        margin-right: 20px !important;
    }
    .main_nav_item a {
        font-size: 12px;
    }
}


