/************
6. 991px
************/

@media only screen and (max-width: 991px) {
    .main_nav_container {
        display: none;
    }
    .logo_container {
        padding-left: 30px;
    }
    .hamburger_container {
        display: block;
    }
    .sidebar {
        margin-top: 120px;
    }
    .newsletter_form_container {
        width: 90%;
    }
    .footer_col {
        margin-bottom: 30px;
    }
    .footer_col:last-child {
        margin-bottom: 0px;
    }
    .main_nav_item a {
        font-size: 10px;
    }
}


