/************
10. 767px
************/

@media only screen and (max-width: 767px) {
    .menu_container {
        right: -100vw;
        width: 100vw;
        height: 100vh;
    }
    .news_post_title_container {
        margin-top: -4px;
        padding-left: 21px;
    }
    .news_post_title a {
        font-size: 27px;
    }
    .newsletter_email {
        width: 100%;
    }
    .newsletter_submit_btn {
        margin-top: 15px;
    }
}


/************
11. 575px
************/

@media only screen and (max-width: 575px) {
    h1 {
        font-size: 24px;
    }
    p {
        font-size: 13px;
    }
    .header {
        height: 74px;
    }
    .logo_container {
        padding-left: 35px;
    }
    .logo span {
        font-size: 30px;
        margin-left: 10px;
    }
    .hamburger_container {
        right: 5px;
    }
    .menu {
        top: 70px;
    }
    .menu_item {
        margin-bottom: 0px;
    }
    .menu_item a {
        font-size: 24px;
    }
    .menu_copyright {
        display: none;
    }
    .menu_social_container {
        margin-top: 50px;
    }
    .menu_close_container {
        right: 30px;
        top: 34px;
    }
    .home {
        height: 372px;
    }
    .home_content {
        padding-top: 17px;
        padding-bottom: 11px;
        padding-left: 22px;
        padding-right: 24px;
    }
    .home_content h1 {
        font-size: 35px;
    }
    .news_post_title_container {
        margin-top: 20px;
        padding-left: 0px;
    }
    .news_post_date {
        width: 55px;
        height: 55px;
    }
    .news_post_date div:first-child {
        font-size: 18px;
    }
    .news_post_date div:last-child {
        font-size: 11px;
    }
    .news_post_title a {
        font-size: 20px;
    }
    .news_post_text {
        margin-top: 20px;
    }
    .news_post_button {
        width: 138px;
        height: 42px;
    }
    .news_post_button a {
        font-size: 14px;
        line-height: 42px;
    }
    .tag {
        height: 35px;
    }
    .tag a {
        font-size: 14px;
        line-height: 35px;
        padding-left: 23px;
        padding-right: 23px;
    }
    .footer_bar {
        padding-top: 49px;
        padding-bottom: 52px;
    }
    .footer_social {
        margin-top: 20px;
    }
    .footer_copyright span {
        font-size: 13px;
    }
}


